@import url(https://fonts.googleapis.com/css2?family=Happy+Monkey&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loginForm-div {
  min-width: 550px;
  margin: 0 auto;
  padding: 50px;
  box-shadow: 2px, 2px, 2px, rgba(0, 0, 0, 1);
}

.loginForm-div input {
  font-size: 19px;
  width: 450px;
  text-align: center;
  font-family: 'Happy Monkey', cursive;
  font-weight: 600;
}

* {
  font-family: 'Happy Monkey', cursive;
}

.login-container {
  background: rgb(47,24,107);
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-form {
  margin-top: -200px;
}

.login-btn {
  background-color: #BF344D;
  color: white;
  border-color: #BF344D;
  width: 150px;
  margin-right: 15px;
  font-weight: 550;
  font-family: 'Happy Monkey', cursive;
}

.login-btn:disabled:hover {
  color: #6D36F7;
}

.login-btn:hover {
  color: white;
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.login-btn:disabled {
  color: #6D36F7;
  -webkit-transform: scale(1.0);
          transform: scale(1.0);
}

.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

.red-border {
  border: 1px solid red;
}

.green-border {
  border: 1px solid green;
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    -webkit-animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  }
}

.App-header {
  align-items: center;
  background: rgb(109,54,247);
  background: -webkit-gradient(linear, left bottom, left top, color-stop(7%, rgba(109,54,247,1)), color-stop(92%, rgba(47,24,107,1)));
  background: linear-gradient(0deg, rgba(109,54,247,1) 7%, rgba(47,24,107,1) 92%);
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  height: 20vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}
.headerGame {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  padding: 20px 10px 20px 10px;
  width: 550px;
  margin: 0px auto;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-color: #fafafa;
  border-radius: 5px;
}

.headerGame-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 25px;
}

.headerGame-img img {
  border-radius: 5px;
  width: 100px;
}

.headerGame-info h3 {
 font-size: 19px;
}

.headerGame-info h4 {
  font-weight: 900;
  margin-top: 5px;
}



.question-container {
  width: 80%;
  margin: 20px auto;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-color: #fafafa;
  padding: 50px;
  border-radius: 5px;
}

#question-text {
  font-size: 25px;
  font-weight: 900;
}

#category {
  margin: 5px 0;
}

.question-btn {
  background-color: #BF344D;
  color: white;
  border-color: #BF344D;
  min-width: 150px;
  margin-right: 15px;
  font-weight: 550;
  font-family: 'Happy Monkey', cursive;
}

.question-btn:hover {
  color: white;
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.question-btn:visited {
  color: white;
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.question-btn:disabled {
  background-color: #BF344DAA;
  -webkit-transform: scale(1.0);
          transform: scale(1.0);
}

.wrong-color {
  background-color: #BF344DAA;
  border: 3px solid red;
}

.correct-color {
  background-color: #BF344DAA;
  border: 3px solid rgb(6, 240, 15);
}

.question-btn-container {
  margin-top: 20px;
}

.next-btn {
  background: rgb(47,24,107);
  color: white;
  border-color: rgb(47,24,107);
  min-width: 150px;
  margin-right: 15px;
  font-weight: 550;
  font-family: 'Happy Monkey', cursive;
  margin-top: 20px;
}

.next-btn:hover {
  color: white;
}
.game-counter {
  font-size: 35px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  padding: 5px 5px;  
  background-color: #fafafa;
  width: 60px;
  margin: 20px auto;
  border-radius: 3px;
  text-align: center;
}

.game-container {
  background: rgb(47,24,107);
  padding-top: 20px;
  height: 80vh;
}
.feedback-container {
  background: rgb(47,24,107);
  height: 80vh;
  padding-top: 20px;
}

.feedback-wrapper {
  background-color: #fafafa;
  width: 550px;
  margin: 40px auto;
  padding: 50px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 5px;
}

.feedback-message {
  font-size: 30px;
  font-weight: 900;
}

.feedback-score {
  font-size: 20px;
}

.feedback-btn {
  width: 180px;
}

.ranking-container {
  background: rgb(47,24,107);
  height: 80vh;
  padding-top: 20px;
}

.ranking-title {
  font-size: 40px;
  color: white;
}

.ranking-wrapper {
  display: flex;
  justify-content: center;
  padding: 20px 10px 20px 10px;
  width: 550px;
  margin: 20px auto;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-color: #fafafa;
  border-radius: 5px;
}

.ranking-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 25px;
}

.ranking-img img {
  border-radius: 5px;
  width: 100px;
}

.ranking-info h3 {
 font-size: 19px;
}

.ranking-info h4 {
  font-weight: 900;
  margin-top: 5px;
}
.footer-container a{
  padding: 10px;
  color: white;
  font-size: 20px;
}

.footer-container h4 {
  color: white;
  font-size: 20px;
  
}

.footer {
  background: rgb(109,54,247);
  background: -webkit-gradient(linear, left bottom, left top, color-stop(7%, rgba(109,54,247,1)), color-stop(92%, rgba(47,24,107,1)));
  background: linear-gradient(0deg, rgba(109,54,247,1) 7%, rgba(47,24,107,1) 92%);
  max-height: 50px;
}
